@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);
body{font:14px "Century Gothic", Futura, sans-serif;margin:0;padding:0;font-family:'Rowdies', cursive}figure{display:inline-block}figcaption{text-align:center;transform:translateY(-5px);overflow:hidden}figcaption span{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}svg{position:relative;height:40px;width:200px}svg polygon{stroke:black}.card{display:inline-block;border:2px solid black;box-shadow:2px 2px 10px -5px;padding:5px;margin:10px;width:200px;height:200px;background-color:transparent;padding-bottom:0px;margin-bottom:0px}#field{position:absolute;width:100%;height:calc(100vh - 80px);padding-top:20px;padding-left:20px;transition:.5s all}#field.dark{box-shadow:inset 0 0 10px 5px #44bd32;background-color:#292b2c}#field.dark span{color:white}#field.dark .card{border:2px solid white}#field.dark polygon{stroke:white}.cutlogo{overflow:hidden}.cutlogo img{margin:-65px 0}.navtop{height:80px}div.custom-switch label{padding-top:1%;line-height:20px;color:white;font-size:1.2em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.buttonStyle1{position:relative;top:50%;left:50%;transform:translate(-50%, -50%);width:150px;height:100px;transition:0.5s}.buttonStyle1 div{position:absolute;width:100%;height:100%}.buttonStyle1 .span_style1{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:auto;height:auto;transition:0.5s;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.buttonStyle1:hover{box-shadow:inset 0 0 5px 5px black}.buttonStyle1:hover .rect_style1{stroke-dasharray:500px 0;cursor:pointer}.span_style1:hover .rect_style1{stroke-dasharray:500px 0;cursor:pointer}.svg_style1,.rect_style1{position:absolute;width:100%;height:100%}.rect_style1{stroke-width:10px;stroke-dasharray:50px 450px;stroke-dashoffset:25px;transition:.5s}.dark .rect_style1{stroke:rgba(46,170,35,0.952)}.dark .buttonStyle1:hover{box-shadow:inset 0 0 5px 5px #fff}

.buttonStyle2{position:relative;top:50%;left:50%;transform:translate(-50%, -50%);width:100px;height:100px;transition:0.5s}.buttonStyle2 div{position:absolute;height:100%;width:100%;border:1px solid black;-webkit-animation:rdm 3s linear 0s infinite;animation:rdm 3s linear 0s infinite;-webkit-animation-play-state:paused;animation-play-state:paused}.buttonStyle2:hover div{-webkit-animation-play-state:running;animation-play-state:running}.buttonStyle2 span{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:auto;height:auto;transition:0.5s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}@-webkit-keyframes rdm{18%{border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-webkit-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-moz-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-ms-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-o-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%}40%{border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-webkit-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-moz-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-ms-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-o-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%}60%{border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-webkit-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-moz-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-ms-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-o-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%}85%{border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-webkit-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-moz-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-ms-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-o-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%}}@keyframes rdm{18%{border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-webkit-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-moz-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-ms-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%;-o-border-radius:50% 40% 30% 90% / 30% 10% 56% 25%}40%{border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-webkit-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-moz-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-ms-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%;-o-border-radius:30% 10% 56% 25% / 50% 40% 30% 90%}60%{border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-webkit-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-moz-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-ms-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%;-o-border-radius:67% 73% 48% 39% / 81% 25% 87% 72%}85%{border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-webkit-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-moz-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-ms-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%;-o-border-radius:81% 25% 87% 72% / 67% 73% 48% 39%}}

